<template>
  <div class="introduction">
    <span>: {{pet.name}}</span>
    <br>
    <span>: {{ pet.birth * 1000 | date }}</span>
    <br>
    <span><span v-if="pet.rare === 2"></span><span v-else></span></span>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import filters from '@/filters'
export default {
  filters,
  data () {
    return {

    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  }
}
</script>

<style lang="scss" scoped>

</style>




